<template>
	<div>
	   <p @click="chooseImage">chooseImage</p>
       <p>res:{{res}}</p>
       <p>error:{{error}}</p>
       <p>{{images}}</p>
       <ul>
           <li v-for="item in images.localId">
               <img :src="item">{{item}}
           </li>
       </ul>
	</div>
</template>
<script type="text/javascript">
import axios from 'axios';
import {JSSDKURL} from "@/api";
export default{
	name:"JSSDK",
	data(){
		return {
            res:"",
            error:"",
            images:{localId:[]}
		}
	},
	async created(){
        let tUrl = encodeURIComponent(window.location.href.split('#')[0]); //需要跳转到哪个页面就写哪个页面的链接
        let datas=await JSSDKURL({"url":tUrl});
        let data=await axios.get("https://api-uat.tianan-life.com/wx-claim-apply/api/callBack/Test?url="+tUrl)
        //let data=JSON.parse(datas.data.replace(/\'/g,"\""));
        data=data.data;
        console.log(data)
        this.$Wx.config({
            debug: true, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
            appId: data.appId, // 必填，公众号的唯一标识
            timestamp: data.timestamp, // 必填，生成签名的时间戳
            nonceStr: data.nonceStr, // 必填，生成签名的随机串
            signature: data.signature,// 必填，签名
            jsApiList: ['getLocalImgData','chooseImage'] // 必填，需要使用的JS接口列表
        });
        this.$Wx.error(res=>{
            console.log(res)
        })
        
        
	},
	methods:{
		/**
         * 选择图片
         */
        chooseImage() {
            let _this = this;
            this.$Wx.chooseImage({
                "sizeType": ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
                "sourceType": ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
                "count": 5, // 默认9
                success: function (res) {
                    for (var i = 0; i < res.localIds.length; i++){
                        _this.getLocalImgData(res.localIds[i])
                    }
                },
                error(err){
                	console.log(err)
                }
            });
        },
        //
        getLocalImgData(localId){
            //
            this.$Wx.getLocalImgData({
                localId: localId, // 图片的localID
                success: function (res) {
                    console.log(res)
                },
                error(err){
                   console.log(err)
                }
            });
        }
	},
	mounted(){
		
	}
}
</script>


